<template>
    <div class="login">
        <div class="login-con">
            <p class="c-FE mt-15 fs-16 mb-20">SCF·仪表盘面板登录</p>
            <div class="width374">
                <div>
                    <el-input placeholder="请输入密码" prefix-icon="el-icon-key" v-model="input.password" type="text"></el-input>
                    <el-button type="primary" v-on:click="login()" :loading="isLoading" class="login-button">登录</el-button>
                </div>
            </div>
        </div>
        <div class="footer">
            <p>Copyright © lanhai, All Rights Reserved.</p>
<!--            <p>重庆汇云锦讯科技有限公司版权所有</p>-->
        </div>
    </div>
</template>
<script>
export default {
    name: 'login',
    mounted() {
        this.$user.checkLogin(() => {
            this.$router.push(this.$user.getReferer());
        })
    },
    components: {},
    data() {
        return {
            isLoading: false,
            input: {
                password: this.$cookie.get('_DASHBOARD_PASSWORD_') || ''
            }
        }
    },
    methods: {
        login() {
            //this.$loading.show('登录中');
            this.isLoading = true
            this.$api.user.login(this.input).then(result => {
                if (result.errCode) {
                    return this.$message.error(result.message)
                }
                this.$cookie.set('_DASHBOARD_PASSWORD_', this.input.password)
                this.$user.init()
            }).catch(error => {
                this.$message.error(error)
            }).finally(() => {
                this.isLoading = false
                //this.$loading.close();
            })
        }
    },
}
</script>
